 margin-left: 16px;\n      position: relative;\n    }\n    .yulan {\n      position: relative;\n      i {\n        &:nth-of-type(1) {\n          box-shadow: -3px 0px 3px -1px #adadad inset;\n          display: inline-block;\n          height: 130px;\n          width: 25px;\n          position: absolute;\n          left: 16px;\n          top: 0;\n          z-index: 2;\n          background-color: white;\n        }\n        &:nth-of-type(2) {\n          box-shadow: 3px 0px 3px -1px #adadad inset;\n          display: inline-block;\n          height: 130px;\n          width: 25px;\n          position: absolute;\n          right: 0;\n          top: 0;\n          z-index: 2;\n          background-color: white;\n        }\n        &:nth-of-type(3) {\n          box-shadow: 0px -3px 3px -1px #adadad inset;\n          display: inline-block;\n          height: 25px;\n          width: 130px;\n          position: absolute;\n          right: 0;\n          top: 0;\n          z-index: 2;\n          background-color: white;\n          &::before {\n            content: \"\";\n            display: inline-block;\n            height: 25px;\n            width: 23px;\n            position: absolute;\n            left: 0;\n            background-color: white;\n          }\n          &::after {\n            content: \"\";\n            display: inline-block;\n            height: 25px;\n            width: 23px;\n            position: absolute;\n            right: 0;\n            background-color: white;\n          }\n        }\n        &:nth-of-type(4) {\n          box-shadow: 0px 3px 3px -1px #adadad inset;\n          display: inline-block;\n          height: 25px;\n          width: 130px;\n          position: absolute;\n          right: 0;\n          top: 105px;\n          z-index: 2;\n          background-color: white;\n          &::before {\n            content: \"\";\n            display: inline-block;\n            height: 25px;\n            width: 23px;\n            position: absolute;\n            left: 0;\n            background-color: white;\n          }\n          &::after {\n            content: \"\";\n            display: inline-block;\n            height: 25px;\n            width: 23px;\n            position: absolute;\n            right: 0;\n            background-color: white;\n          }\n        }\n      }\n    }\n  }\n  // // 预览\n  // .previewBox,\n  // .previewBoxRound {\n\n  // }\n  :deep(.cropper-container) {\n    // margin: 24px;\n    box-sizing: border-box;\n    // width: calc(100% - 100px);\n    .cropper-wrap-box {\n      // width: 100%;\n    }\n    &:nth-of-type(2){\n      display: none;\n    }\n  }\n  // 裁剪显示图片\n  #cropImg {\n    // height: 450px;\n    width: 50% !important;\n    height: 50% !important;\n    // max-width: 100%;\n    // max-height: 50vh;\n    box-shadow: 0 0 5px #adadad;\n  }\n  .crop_btn_top {\n    height: 50px;\n    flex-grow: 0;\n    display: flex;\n    align-items: center;\n    justify-content: flex-end;\n    .el-icon {\n      cursor: pointer;\n      width: 40px;\n      height: 40px;\n      margin-right: -12px;\n      &:hover {\n        color: var(--el-color-primary);\n      }\n    }\n    svg {\n      font-size: 12px;\n    }\n  }\n  .crop_btn {\n    height: 60px;\n    flex-grow: 0;\n    flex-shrink: 0;\n    display: flex;\n    align-items: center;\n    // padding: 0 24px;\n    justify-content: flex-end;\n    .el-button {\n      min-width: var(--cur-dialog-btn-width);\n    }\n  }\n}\n"],"sourceRoot":""}]);
